import React,{useEffect,useState} from 'react'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import {Grid} from '@material-ui/core';
import {domain} from '../../../connection/Config'
import Rejected from './Rejected';
import Divider from '@material-ui/core/Divider';
import Loading from '../../Loading_spinner/Loading';
import '../../../css/orders.css'
import firebase from '../../../connection/firebase' 

export default function Orders_popup({setPopupTriger,setRefresh,newOrder,token,setNewOrder,setLoader}) {
    const [open, setOpen] = React.useState(false);
    const [time,setTime]=useState("")
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const db = firebase.firestore(); 
    useEffect(() => {
        setOpen(true);
    }, [])
     
    
      const delivery= async(index)=>{
        setLoader(true)
        try{
          if(time !==""){
           let obj={
             time:time,
             id:token
           }
          let snapshot = await db.collection('orders').doc(newOrder[index].id).get()
  
           let full_order={...snapshot.data(),id:snapshot.id}
             if(full_order.update_time){
                 full_order.update_time.push(obj)
                  snapshot = await db.collection('orders').doc(newOrder[index].id).update({
                 update_time:full_order.update_time
                   })
                  }
                  else{
                    let arr=[obj]
                    snapshot = await db.collection('orders').doc(newOrder[index].id)
                    let setWithMerge =  snapshot.set({
                      update_time:arr, 
                    },{merge:true})
                  }
          }
      
            
          

          const res2 = await fetch(`${domain}/orders/delivery`,
          {
              method: 'POST',
              headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
              },
              body: JSON.stringify(
                  {
                      id:token,
                      items:newOrder[index],
                      user_id:newOrder[index].user_id
                  }
                  ) 
          }
          )
         const data= await res2.json()
          console.log(data)
       if(data.user && data.cooker){
        let snapshot=db.collection('deliveries').add({
          delivery_price:15,
          delivery_status:'בדיקה',
          driver_uid:"",
          drop_off_address:newOrder[index].address||"",
          drop_off_geo_point:newOrder[index].location||"",
          drop_off_name:newOrder[index].name||"",
          drop_off_phone:data.user.phone||"",
          drop_off_timestamp:"",
          order_id:newOrder[index].id,
          pickup_adress:data.cooker.address||"",
          pickup_geo_point:data.cooker.geo_point,
          pickup_name:data.cooker.vendor||"",
          pickup_time:data.cooker.phone_number||"",
          shipping_id:"",
          arrival_time:newOrder[index].arrival_time,
          update_arrival_time:time||''
         })
       }
      
       setTime('')
       setRefresh(Math.random())
     
      setLoader(false)
        }
        catch(err){
          console.log(err)
          
        }
      }

const handleclose=()=>{
  setRefresh(Math.random())
  setOpen(false)
  setPopupTriger(false)
 
}
    
    return (
      <div>
        <Loading
       />
        <Dialog
          fullScreen={fullScreen}
          open={open}
          aria-labelledby="responsive-dialog-title"
        >
          {newOrder.map((el,index)=>{
            return(
              <>
              {!el.approved?
               <form>
                 
               <DialogTitle id="responsive-dialog-title">התקבלה הזמנה חדשה</DialogTitle>
               <p>במקרה ולא תוכלו לבצע את ההזמנה התקשרו לרום</p>
               <DialogContent>
               <DialogContentText>
                  {el.items.map(item=>{
                    return(
                     <h3>.{item.amount}  {item.name}   </h3>
                    ) 
                  })
                  }
                </DialogContentText>
                <Divider  style={{marginBottom:'5px',marginTop:'10px'}} variant="right" />
                <DialogContentText style={{fontSize:'18px'}}>
                  <h3> למתי : <span className='time'>{newOrder[index].arrival_time}</span></h3>
                <h3>צפי הגעת המשלוח : <span className='time'>{new Date().toLocaleString(undefined, {
    month: "long", day: "numeric", 
    hour: "numeric", minute:"numeric"})}</span></h3> 
             
                ? האם המשלוח יגיע בזמן
                : אם לא ציין בכמה זמן תתעכב
                </DialogContentText>
               
                 <DialogContentText className='bt-warper'>
                 <input  onChange={(e)=>{setTime(e.target.value)}} style={{width:"230px",marginLeft:'10px',height:'30px'}} type='time'/>
                 </DialogContentText>
               </DialogContent>
                  <Grid className='bt-warper'>
                  <Rejected 
                  setRefresh={setRefresh}
                  newOrder={newOrder}
                  index={index}
                  token={token}
                  />
                 <Button onClick={()=>{delivery(index)}}  type='button' className='agree' variant='outlined' >
                  אישור
                 </Button>
                  </Grid>
                 
                 </form>
              :
                !el.approved.includes(token)?
                <form>
               <DialogTitle id="responsive-dialog-title">התקבלה הזמנה חדשה</DialogTitle>
               <p>במקרה ולא תוכלו לבצע את ההזמנה התקשרו לרום</p>
               <p>{el.id}</p>
               <DialogContent>
               <DialogContentText>
                  {el.items.map(item=>{
                    return(
                     <h3>.{item.amount}  {item.name}   </h3>
                    ) 
                  })
                  }
                </DialogContentText>
                <Divider  style={{marginBottom:'5px',marginTop:'10px'}} variant="right" />
                <DialogContentText style={{fontSize:'18px'}}>
                  <h3> למתי : <span className='time'>{newOrder[index].arrival_time}</span></h3>
                <h3>צפי הגעת המשלוח : <span className='time'>{new Date().toLocaleString(undefined, {
    month: "long", day: "numeric", 
    hour: "numeric", minute:"numeric"})}</span></h3> 
             
                ? האם המשלוח יגיע בזמן
                : אם לא ציין בכמה זמן תתעכב
                </DialogContentText>
               
                 <DialogContentText className='bt-warper'>
                 <input  onChange={(e)=>{setTime(e.target.value)}} style={{width:"230px",marginLeft:'10px',height:'30px'}} type='time'/>
                 </DialogContentText>
               </DialogContent>
                  <Grid className='bt-warper'>
                  <Rejected 
                     token={token}
                  newOrder={newOrder}
                  index={index}
                  setRefresh={setRefresh}

                  />
                 <Button onClick={()=>{delivery(index)}}  type='button' className='agree' variant='outlined' >
                  אישור
                 </Button>
                  </Grid>
                 
                 </form>
              :handleclose}
             
                </>
            )
          })}
           
        </Dialog>
      </div>
    );
}
